
// ---------------------------------------- INPUT
.k-input-field {
    display: block;
    position: relative;
    padding: 0px;
    margin-bottom: 24px;
    width: 100%;

    .k-field {
      position: relative;

        .k-input {
            font-family: $Main-font;
            font-size: 18px;
            height: 48px;
            background-color: #FFFFFF;
            border: 1px solid $c-strokes;
            border-radius: 4px;
            width: 100%;
            padding: 10px 25px 0px 25px;
            outline: none;
            text-align: left;
            @include hidden-placeholder;
            @mixin label-active {
              ~ .k-input-label {
                  color: $c-disabled;
                  top: 12px;
                  font-size: 14px;
              }
              // Icon
              + .icon-input { margin-top: 2px }
            }

            &:focus,
            &:active {
                @include label-active;
            }

            &:not(:placeholder-shown) {
                @include label-active;
            }

            &:-webkit-autofill {
                @include label-active;
            }

            &:not(:-ms-input-placeholder) {
                @include label-active;
            }

            &:read-only, &:disabled {
                background-color: rgba(140,140,140,.1);;
            }
        }

        .k-input-label {
            font-size: 18px;
            color: $c-disabled;
            position: absolute;
            left: 25px;
            top: 50%;
            transform: translateY(-50%);
            pointer-events: none;
            transition: 0.2s ease all;
            right: 0px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            margin-right: 22px;
            text-align: left;
        }
    }

    // K-field with icon
    .k-field-icon {
        .k-input { padding: 10px 20px 0px 38px; }
        .k-input-label { left: 38px; }

        // Icon
        .icon-input {
          position: absolute;
          width: 14px;
          left: 15px;
          top: 50%;
          transform: translateY(-50%);
          transition: 0.2s ease all;
        } 
    }

    // Error
    .k-error {
      color: $c-alert;
      align-items: center;
      margin: 8px 0px;
      transition: opacity 0.3s;
      pointer-events: none;
      visibility: hidden;
      opacity: 0;
      position: absolute;
      z-index: -10;
      display: none;

      .icon-error { 
        width: 14px;
        margin-right: 7px;
        margin-top: 1.8px;
      }
    }
}

// SELECT 2
.select2-container--default .select2-selection--single, .select2-results__option  {
  font-size: 18px !important;
}

.select2-container--default .selection .select2-selection--single,
.select2-container--default .selection .select2-selection--single {
  border-radius: 5px !important;
  box-shadow: 0px 0px 0px 0px !important;
  border: 1px solid #cdcdcd !important;
}
.select2-container,
.select2-container--open {
  width: auto !important;
}
.select2-container--default .selection .select2-selection__rendered {
  font-size: 18px !important;
}

// ---------------------------------------- SELECT2
.k-select-field {
  margin-bottom: 24px;
  
  .k-select-label {
      font-size: 18px;
      display: block;
      font-weight: bold;
      margin-bottom: 7px;
  }
}

.select2-selection {  
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
}

// ---------------------------------------- ERRORS input/select
// Error
.k-error {
    color: $c-alert;
    align-items: center;
    margin: 8px 0px;
    transition: opacity 0.3s;
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
    position: absolute;
    z-index: -10;
    display: none;

    .icon-error {
        width: 14px;
        margin-right: 7px;
        margin-top: 1.8px;
    }
}

// Error active
.k-field-error {
    // Input
    .k-field {
        .k-input {
          border: 1px solid $c-alert;
        }
    }
    // Select
    .select2-selection {
        border: 1px solid $c-alert !important;
    }
    
    .k-error {
      position: relative;
      visibility: visible;
      opacity: 1;
      z-index: 1;
      display: flex;
      border: 0px !important;

      i { margin-right: 5px; }
    }
  }

.k-error-show {
    position: relative !important;
    visibility: visible !important; 
    opacity: 1 !important;
    z-index: 1 !important;
    display: flex !important; 
}


// Radio button 
.k-radio-field {
  position: relative;
  width: auto;
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  input {
      display: none;
      &:checked {
          & ~ .k-radio-button {
              &:before {
                  display: block;
              }
          }
      }
  }
  .k-radio-button {
      width: 16px;
      height: 16px;
      min-width: 16px;
      min-height: 16px;
      position: relative;
      border-radius: 50%;
      background-color: #fff;
      border: 1px solid #999;
      box-sizing: border-box;
      pointer-events: none;
      &:before {
          display: none;
          content: "";
          width: 8px;
          height: 8px;
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          margin: auto;
          background-color: #f05825;
          border-radius: 50%;
      }
  }
  p {
      margin: 0px;
      margin-left: 14px;
      pointer-events: none;
  }
}