.k-toast-alert {
    background-color: $c-main-2;
    padding: 15px 10px;
    color: #FFFFFF;
    text-align: center;
    box-sizing: border-box;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 6;
    transition: opacity 0.4s;
    animation: kToastAnimation 0.6s;

    .k-toast-alert-content {
        max-width: 1300px;
        margin: 0% auto;
        display: flex;
        align-items: center;
        justify-content: center;

        i { margin-right: 12px; font-size: 18px; }
        p {
            margin: 0px;
            line-height: 120%;
            color: #FFFFFF;
        }
    }

    @keyframes kToastAnimation {
        0% { opacity: 0; top: -30px; }
        100% { opacity: 1; top: 0; }
    }
}