$Main-font: BariolRegular, "Bariol Regular", "Helvetica Neue", Helvetica, Arial, sans-serif;

// Typography
.k-h1,
.k-h2,
.k-h3,
.k-h4,
.k-h5,
.k-h6,
.k-cta,
.k-content,
.k-link,
.k-notifications,
.k-conditions {
    color: $c-text;
    font-style: normal;
    font-weight: 400;
    text-transform: none;
    letter-spacing: 0;
    line-height: 1.2;
}

.k-h1 {
    font-size: 37px;
}
.k-h2 {
    font-size: 30px;
}
.k-h3 {
    font-size: 28px;
}
.k-h4 {
    font-size: 24px;
}
.k-h5 {
    font-size: 22px;
}
.k-h6 {
    font-size: 19px;
}
.k-cta {
    font-size: 16px;
}
.k-content {
    font-size: 18px;
    line-height: 1.8;
}
.k-link {
    font-size: inherit;
    line-height: inherit;
    color: $c-main;
}
.k-notifications {
    font-size: 16px;
    line-height: 1.5%;
}
.k-conditions {
    font-size: 14px;
    line-height: 1.5%;
}
.k-text-hover {
    &:hover {
        @include transition(color 0.3s);
        color: $c-main;
    }
}

.k-link {
    text-decoration: none;
    position: relative;

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        margin: auto;
        width: 0;
        height: 1px;
        background-color: $c-main;
        transition-duration: 0.3s;
    }

    &:hover {
        &:after {
            width: 100%;
        }
    }
}

@media (min-width: 1200px) {
    .k-h2 {
        font-size: 34px;
    }
    .k-h3 {
        font-size: 32px;
    }
    .k-h4 {
        font-size: 28px;
    }
    .k-h5 {
        font-size: 26px;
    }
    .k-h6 {
        font-size: 21px;
    }
}

@media (min-width: 1600px) {
    .k-h1 {
        font-size: 47px;
    }
    .k-h2 {
        font-size: 37px;
    }
    .k-h3 {
        font-size: 34px;
    }
    .k-h4 {
        font-size: 30px;
    }
    .k-h5 {
        font-size: 27px;
    }
    .k-h6 {
        font-size: 23px;
    }
    .k-content {
        font-size: 19px;
    }
}

// -- Text
.k-text-center {
    text-align: center;
}
.k-text-left {
    text-align: left;
}
.k-text-right {
    text-align: right;
}
.k-bold {
    font-weight: bold;
}
.k-line {
    text-decoration: line-through;
}
.k-italic {
    font-style: italic;
}

// Line heights
.k-height-0 {
    line-height: 0%;
}
.k-height-110 {
    line-height: 110%;
}
.k-height-120 {
    line-height: 120%;
}
.k-height-180 {
    line-height: 180%;
}
