.k-modal-background {
    display: none;
    position: fixed;
    align-items: center;
    justify-content: center;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.4);
    padding: 50px 12px 70px 12px;
    z-index: 30;
    box-sizing: border-box;
    
    .select2-container,
    .select2-container--open {
        width: auto !important;
    }

    .select2-container--open {
        .select2-dropdown {
            margin-left: -7px;
            margin-top: -8px;
        }
    }
    
    * { box-sizing: border-box; }

    .k-modal {
        background-color: #f7f7f7;
        padding: 0;
        border: 8px solid #fff;
        border-radius: 16px;
        max-width: 400px;
        padding: 15px 20px 35px;
        display: flex;
        flex-flow: column;
        // position: fixed;
        position: relative;
        // top: 80px;
        top: 0px;
        bottom: auto;
        // left: 10px;
        // right: 10px;
        margin: auto;
        opacity: 0;
        visibility: hidden;
        box-shadow: 0 5px 11px -4px rgba(0,0,0,.5);
        -webkit-transition-duration: .3s;
        -o-transition-duration: .3s;
        transition-duration: .3s;
        z-index: 2;
        // max-height: calc(100vh - 160px);
        overflow-y: auto;
        overflow-x: hidden;
        box-sizing: border-box;
        margin: auto;
        width: 100%;

        .k-modal-close-wrapper {
            color: white;
            font-size: 28px;
            font-weight: bold;
            background: #ffffff;
            border-radius: 50%;
            width: 59px;
            height: 59px;
            display: flex;
            position: absolute;
            right: -20px;
            top: -20px;
            flex-flow: row;
            align-items: center;
            justify-content: center;
            transition: 0.3s ease-in-out;
            color: #122448;
            &:hover, &:focus {
                text-decoration: none;
                cursor: pointer;
                width: 65px;
                height: 65px;
            }
            .k-modal-close-icon {
                position: absolute;
                top: 15px;
                right: 30px;
                line-height: 120%;
            }
        }
        &::-webkit-scrollbar {
            width: 8px;
            height: 8px;
            }
        &::-webkit-scrollbar-track {
        background: #f7f7f7;
        }

        &::-webkit-scrollbar-corner {
        background: #f7f7f7;
        }

        &::-webkit-scrollbar-thumb {
        background: #999999;
        border-radius: 1px;
        border: 2px solid #f7f7f7;
        }

        &::-webkit-scrollbar-thumb:hover {
        background: #c4c4c4;
        }
    }
    .k-displayed-modal {
        visibility: visible;
        opacity: 1;
    }
}