.k-pagination {
	z-index: 2;
	width: 100%;
	height: 32px;
	text-align: center;
	margin: 30px 0 40px !important;
	ul {
		display: inline-block;
		margin: 0;
		li {
			a {
				border-radius: 50%;
				background: #f0f0f0;
				box-shadow: 0 2px 2px rgba(0, 0, 0, .118829);
				margin-left: 10px;
				&:hover {
					background-color: $c-main;
				}
			}
			span {
				border-radius: 50%;
				background: #f0f0f0;
				box-shadow: 0 2px 2px rgba(0, 0, 0, .118829);
				margin-left: 10px;
				&:hover {
					background-color: $c-main;
				}
			}
			span[class=current] {
				background-color: $c-main;
			}
		}
	}
	li {
		display: inline;
	}
	a {
		float: left;
		width: 32px;
		height: 32px;
		margin-left: -1px;
		font-size: 13px;
		font-weight: 400;
		line-height: 32px;
		text-decoration: none;
		color: $c-text;
		&:hover {
			color: #fff;
			background-color: $c-text;
		}
	}
	span {
		float: left;
		width: 32px;
		height: 32px;
		margin-left: -1px;
		font-size: 13px;
		font-weight: 400;
		line-height: 32px;
		text-decoration: none;
		color: $c-text;
	}
	span.pages {
		display: none;
	}
	span.dots {
		color: #fff;
		background-color: $c-text;
	}
	span.current {
		color: #fff;
		background-color: $c-text;
	}
}
@media (max-width: 767px) {
	.k-pagination {
		a.prev {
			display: none;
		}
		a.next {
			display: none;
		}
		a.prev-next {
			display: none;
		}
	}
}
@media (max-width:767px) {
	.k-pagination {
		a.next {
			display: block !important;
		}
		a.prev {
			display: block !important;
		}
		a.prev-next {
			display: block !important;
		}
	}
}
