.k-accordion {
  .k-accordion-wrapper {
    margin: 18px 0;

    .k-accordion-button {
      background-color: #fff;
      color: #333333;
      cursor: pointer;
      padding-left: 18px;
      width: 100%;
      height: 45px;
      border: 0.5px solid #cdcdcd;
      border-radius: 4px;
      text-align: left;
      outline: none;
      font-size: 21px;
      font-weight: bold;
      transition: 0.4s;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .k-accordion-label {
        max-width: 90%;
      }

      i { 
        margin-right: 10px;
        z-index: 0;

        &::before {
          font-size: 15px;
        }
      }

      .icon-open-accordion  {
        transition: transform 0.3s;
      }

      &:focus {
        border-radius: 4px 4px 0px 0px;
      }
    }

    .k-accordion-open {
      .icon-open-accordion {
        transform: rotate(180deg);
      }
    }

    .k-accordion-button {
      height: 55px;
      padding-top: 0px;
      padding-bottom: 0px;
  }

    .k-accordion-content {
      background-color: #fff;
      max-height: 0;
      overflow: hidden;
      transition: all 0.3s ease-out;
      border-radius: 0px 0px 4px 4px;

      > * {
        padding: 18px;
      }
    }

    .k-accordion-show {
      border-color: #CDCDCD;
      border-style: solid;
      border-width: 0px 0.5px 0.5px 0.5px;
    }

    .k-accordion-hidden {
      max-height: 0;
    }
  }
}
