// ---------------------------------------- Switch
// With text
.k-container-switch {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .k-switch {
        margin: 0px;
        transform: translateX(-30%);
    }

    .k-swtich-left {
        margin: 0px;
        margin-right: 32px;
    }
    .k-swtich-right {
        margin: 0px;
        margin-right: 20px;
    }
}

// Only switch
.k-switch {
    position: relative;
    top: 0;
    right: 0;
    margin: auto;
    display: inline-block;
    width: 36px;
    height: 18px;

    input {
        opacity: 0;
        width: 0;
        height: 0;

        &:focus {
            outline: none;
        }
        &:checked + .slider {
            box-shadow: 0 0 1px #122448;
            background-color: $c-main-2;
        }
        &:checked + .slider:before {
            -webkit-transform: translateX(18px);
            -ms-transform: translateX(18px);
            transform: translateX(18px);
        }
    }

    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        -webkit-transition: 0.4s;
        transition: 0.4s;
        background-color: $c-disabled;
        margin: 0px;
        width: 100%;

        &:before {
            position: absolute;
            content: "";
            height: 14px;
            width: 14px;
            left: 2px;
            bottom: 2px;
            background-color: #FFFFFF;
            -webkit-transition: 0.4s;
            transition: 0.4s;
        }
    }

    .round {
        border-radius: 34px;
        &:before {
            border-radius: 50%;
        }
    }

    @media (min-width: 1024px) {
        width: 48px;
        height: 25px;

        input {
            &:checked + .slider:before {
                -webkit-transform: translateX(23px);
                -ms-transform: translateX(23px);
                transform: translateX(23px);
            }
        }
        
        .slider {
            &:before {
                height: 21px;
                width: 21px;
            }
        }
    }
}