// Article card 
.k-article-card {
    width: 100%;
    background-color: #fff;
    border-radius: 10px;
    @include box-shadow(0px 2px 4px #CDCDCD);
    
    .entry-featured {
        height: auto;
        width: 100%;
        bottom: 0px;
        position: relative;
        border-radius: 10px 10px 0 0;
        cursor: pointer;
        background: {
            size: cover;
            position: center center;
            repeat: no-repeat
        }

        a { text-decoration: none; }
        
        &:hover {
            .entry-thumb {
                background-color: rgba(0,0,0,.8);
                p { margin-top: 0px; opacity: 1 }
            }
        }

        .entry-thumb {
            height: 190px;
            position: relative;
            color: #FFFFFF;
            padding: 7px 9px 7px 0px;
            @include displayFlex;
            @include align-items(center);
            @include justify-content(center);
            @include transition(background-color 0.3s);
            &:before { content: none; }

            .post-excerpt {
                text-align: center;
                max-width: 90%;
                margin-top: 10%;
                margin-bottom: 0px;
                opacity: 0;
                @include transition(margin-top 0.3s, opacity 0.3s);
            }
        }
    }

    .entry-wrap {
        height: 150px;
        .entry-header {
            position: relative;
            & > p {
                margin: 0;
                &:first-child {
                    color: $c-main;
                }
            }
            min-height: 80px;
            padding: 10px;
            position: relative;
            .entry-title {
                height: 90px;
                line-height: 24px!important;
                a { text-decoration: none; color: $c-text; } 
            }
            .desktop-date {
                display: none;
            }
            .mobile-date {
                display: block;
            }
    
            .p-extra-meta {
                margin-top: 1em;
                width: 90%;
                font-size: 0.75em;
                position: absolute;
                bottom: 0px;
                text-align: center;
                margin-bottom: 14px
            }
            .entry-social-list {
                position:absolute;
                right: 20px;
                bottom: 30px;
                overflow: hidden;
                & > img {
                    display: block;
                    position: relative;
                    top: 20px;
                    cursor: pointer;
                    @include transition-duration(0.3s);
                }
                .social-network-wrapper {
                    width: 0;
                    height: 0;
                    position: absolute;
                    background-color: #fff;
                    top: 0;
                    left: 0;
                    z-index: 1;
                    overflow: hidden;
                    @include box-shadow(0px 2px 4px #CDCDCD);
                    @include transition-duration(0.3s);
                }
            }
            .expanded-entry-social-list {
                overflow: visible;
                & > img {
                    top: 0;
                }
                .social-network-wrapper {
                    width: 130px;
                    height: 175px;
                    top: -185px;
                    & > p.k-content {
                        display: none;
                    }
                    ul {
                        margin: 0;
                        list-style: none;
                        li {
                            height: 35px;
                            width: 100%;
                            padding: 0 10px;
                            a{
                                height: 100%;
                                @include displayFlex;
                                @include align-items(center);
                                img {
                                    margin-right: 5px;
                                }
                                p {
                                    margin: 0;
                                    color: #333;
                                }
                            }
                        }
                    }
                }
            }
            &:hover {
                .entry-social-list {
                    & > img {
                        top: 0;
                    }
                }
            }
        }
        @media (min-width: 760px) {
            height: 30%;
    
            .entry-header { 
                height: 100%;
                min-height: 0px;
                
                .entry-title {
                    height: 55px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    line-height: 24px !important;
                    margin-top: 5px;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    transition: color 0.3s;
                    
                    &:hover {
                        color: $c-main;
                    }
                }
                .desktop-date {
                    display: block;
                }
                .mobile-date {
                    display: none;
                }
            }
        }
        @media (max-width: 1024px) {
            .entry-header {
                .entry-social-list {
                    & > img {
                        top: 0;
                        width: 12px;
                    }
                    .social-network-wrapper {
                        position: fixed;
                        width: 100%;
                        top: auto;
                        bottom: 0;
                        left: 0;
                        z-index: 1;
                        @include box-shadow(0px 2px 4px #CDCDCD);
                        & > p.k-content {
                            display: block;
                            width: 178px;
                            font-weight: normal;
                            margin: 10px auto 20px;
                        }
                        ul {
                            width: 80%;
                            margin: auto;
                        }
                    }
                }
                .expanded-entry-social-list {
                    .social-network-wrapper {
                        height: 240px;
                        max-height: 80vh;
                    }
                }
            }
        }
        @media (min-width: 1024px) {
        
            .entry-header {
                .entry-title {
                    height: 68px;
                    line-height: 30px!important;
                }
                .entry-social-list {
                    bottom: 20px;
                }
            }
        }
    }

    @media (min-width: 760px) {
        height: 355px;

        .entry-featured {
            height: 70%;

            .entry-thumb { 
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
            }
        }
    }

    @media (min-width: 1024px) {
        height: 420px;
    }
    @media (max-width: 400px) {
        .entry-featured {
            .entry-thumb {
                height: 160px;
            }
        }
        .entry-wrap {
            .entry-header {
                .entry-title {
                    height: 120px;
                    overflow: hidden;
                }
            }
        }
    }
}


// Horizontal card 
.k-trending-card {
    display: inline-block;
    height: 155px;
    width: calc(50% - 10px);
    background-color: #fff;
    margin-left: 10px;
    margin-bottom: 14px;
    border-radius: 10px;
    box-shadow: 0px 2px 2px #cdcdcd;
    &:first-child {
        float: left;
        height: 663px;
        margin-left: 0;
        margin-right: 10px;
        margin-bottom: 0;
        border-radius: 10px;
        .entry-featured {
            width: 100%;
            display: block;
            height: calc(100% - 155px);
            border-radius: 10px 10px 0 0;
        }
        .entry-wrap {
            width: 100%;
            height: 155px;
        }
    }
    .entry-featured {
        width: 35%;
        height: 100%;
        display: inline-block;
        float: left;
        background-size: cover;
        background-position: center;
        border-radius: 10px 0 0 10px;
    }
    .entry-wrap {
        width: 65%;
        display: inline-block;
        .entry-header {
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding-left: 20px;
            .entry-social-list .social-network-wrapper {
                z-index: 1;
            }
            p.k-conditions {
                display: none;
            }
        }
        @media (min-width: 760px) {
            height: 100%;
        }
    }
}
@media (max-width: 1200px) {
    .k-trending-card {
        height: 127px;
        &:first-child {
            height: 550px;
        }
    }
}
@media (max-width: 900px) {
    .k-trending-card {
        display: block;
        width: 100%;
        max-width: 100%;
        margin-left: 0;
        &:first-child {
            float: none;
            height: 350px;
            margin-bottom: 10px;
            .entry-featured {
                height: calc(100% - 100px);
            }
            .entry-wrap {
                height: 100px;
                .entry-header {
                    .entry-title {
                        max-height: 55px;
                    }
                }
            }
        }
        .entry-wrap {
            height: 100%;
            .entry-header {
                height: 100%;
                padding: 0px 20px 0px 10px;
                &:before {
                    display: none;
                }
            }
        }
        &:last-child {
            display: none;
        }
    }
}
@media (max-width: 600px) {
    .k-trending-card {
        &:first-child {
            .entry-wrap {
                .entry-header {
                    .entry-title {
                        height: 60px;
                    }
                }
            }
        }
        .entry-wrap {
            .entry-header {
                .entry-title {
                    height: 70px;
                    overflow: hidden;
                }
            }
        }
    }
}
