.k-carousel {
    margin-top: 40px;
    margin-bottom: 40px;
    position: relative;
    overflow: hidden;

    &:hover, &:focus {
        outline: none;
        border: none;
    }

    .k-carousel-arrow {
        position: absolute;
        top: 0;
        bottom: 0;
        opacity: 0;
        transition: opacity 0.3s;
        transform: translate(0,0);
        z-index: 20;
        height: auto;
        width: 130px;
        background-color: transparent;
        border: 0px;
        &:focus { outline: none; }
        
        i { font-size: 20px; }
    }
    .k-carousel-arrow-prev { 
        left: -45px; 
        padding: 0px;
        &:hover { color: initial; }
    }
    .k-carousel-arrow-next { 
        right: -45px; 
        padding: 0px;
        &:hover { color: initial; }
    }

    &:hover, &:focus {
        .k-carousel-arrow { opacity: 1; }
    }

    .swiper-wrapper {
        transition-timing-function: linear !important;
    }

    .k-carousel-boxes-wrapper {
        position: relative;
        overflow: hidden;
    
        .k-carousel-box {
            background-repeat: no-repeat;
            background-size: cover;
            margin-right: 30px;
            // height: 220px;
            height: auto;
            // width: 100%;
            position: absolute;
            top: 0;

            .link-container {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                cursor: pointer;
                text-decoration: none;
                
                &:hover {
                    border: 0px;
                    outline: 0px;
                }
            }

            .bg-box {
                margin: auto;
                background: {
                    size: cover;
                    position: center center;
                    repeat: no-repeat
                }
                display: flex !important;
                align-items: flex-end;
                height: 100%;
            }
        }
    
        @media (min-width: 600px) {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    
    }

    // DOTS
    .k-carousel-dots-wrapper {
        position: absolute;
        width: 100%;
        bottom: 10px;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        list-style: none;
        margin: 0px;

        .k-carousel-dot {
            border-radius: 50%;
            width: 16px;
            height: 16px;
            border: 1px solid $c-cta-hover;
            background-color: transparent;
            transition: background-color 0.4s;
            cursor: pointer;
            margin-right: 15px;
            &:last-child { margin-right: 0px; }
        }
        .k-carousel-dot-active { background-color: $c-cta-hover; }
    } 

    @media (min-width: 700px) {
        .k-carousel-arrow {
            i { font-size: 30px; }
        }
        .k-carousel-arrow-prev { left: -35px; }
        .k-carousel-arrow-next{ right: -35px; }
    }
}


// CAROUSEL POSTS 
.k-carousel-posts {
    .k-carousel-arrow {        
        i {
            background-color: rgba(150, 150, 150, 0.4);
            border-radius: 50%;
            width: 65px;
            padding: 20px;
        }
    }

    .k-carousel-boxes-wrapper {
        .k-carousel-box {
            margin-right: 30px;
            height: 220px;
            position: absolute;
            top: 0;

            .bg-box {
                .bottom-info {
                    padding: 20px 15px 20px 15px;
                    background-color: rgba(255,255,255,0.7);
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    width: 100%;
                    z-index: 2;
                    height: 96px;

                    .title {
                        margin: 0px;
                        max-height: 57px;
                        overflow: hidden;
                    }
                }
            }
        }

        @media (min-width: 600px) {
            .k-carousel-box {
                height: 300px;
            }
        }

        @media (min-width: 1200px) {
            .k-carousel-box {
                .bg-box {
                    .bottom-info {
                        .title {
                            font-size: 22px !important;
                        }
                    }
                }
            }
        }

        @media (min-width: 1500px) {
            .k-carousel-box {
                height: 380px;
                
                .bg-box {
                    .bottom-info {
                        padding: 30px 25px 30px 25px;
                        height: 100px;

                        .title {
                            max-height: 65px;font-size: 27px !important;
                        }
                    }
                }
            }
        }
    }

    @media (min-width: 700px) {
        .k-carousel-arrow {
            i { 
                font-size: 30px;
                padding: 50px; 
            }
        }
        .k-carousel-arrow-prev { left: -30px; }
        .k-carousel-arrow-next{ right: -30px; }
    }
}

// SIMPLE CAROAUSEL 
.k-carousel-simple {
    position: relative;
    width: 100%;
    overflow-x: hidden;
    margin-bottom: 0px;

    .k-carousel-boxes-wrapper {
        width: 100%;
        overflow-x: scroll;
        overflow-y: hidden;
        display: inline-flex;
        justify-content: flex-start;
        align-items: normal;
        white-space: nowrap;
        transition: all 1s;
        position: relative;
        padding-bottom: 20px;
        cursor: pointer;

        &::-webkit-scrollbar { display: none; }
        scrollbar-width: none;
        -ms-overflow-style: none;

        .k-carousel-box {
            // display: inline-block;
            display: inline-flex;
            background-repeat: no-repeat;
            background-size: cover;
            position: relative;
            top: auto;

            div { white-space: normal; }
        }
    }

    .dragging-list {
        cursor: grabbing;
        .k-carousel-box {
            pointer-events: none;
            * {
                pointer-events: none;
            }
        }
    }

    .k-carousel-arrow {
        top: 50%;
        padding: 0px;
        height: auto;
        transform: translate(0px, -80%);
        bottom: auto;
        width: 70px;
    }
    .k-carousel-arrow-prev { 
        left: -20px; 
    }
    .k-carousel-arrow-next { 
        right: -20px; 
    }

    @media (min-width: 1024px) {
        .k-carousel-arrow-prev { 
            left: -15px; 
        }
        .k-carousel-arrow-next { 
            right: -15px; 
        }
    }
}