// PERCENTAJE
@for $i from 1 through 100 {
    .k-width-#{$i} {
        width: $i * 1%;
    }
}


@media (min-width: 600px) {
    @for $i from 1 through 100 {
        .k-width-md-#{$i} {
            width: $i * 1%;
        }
    }
}

@media (min-width: 1024px) {
    @for $i from 1 through 100 {
        .k-width-lg-#{$i} {
            width: $i * 1%;
        }
    }
}

@media (min-width: 1400px) {
    @for $i from 1 through 100 {
        .k-width-xl-#{$i} {
            width: $i * 1%;
        }
    }
}

