// HORIZONTAL
.k-scroll-element-h {
  overflow-x: scroll;
  white-space: nowrap;

  &::-webkit-scrollbar {
    height: 8px;
  }

  &::-webkit-scrollbar-track {
    background: #f7f7f7;
  }

  &::-webkit-scrollbar-thumb {
    background: #999999;
    border-radius: 1px;
    border: 2px solid #f7f7f7;

    &:hover {
      background: #c4c4c4;
    }
  }
}

