// ---------------------------------------- INPUT floating label
.k-input-field-floating {
    display: block;
    position: relative;
    padding: 0px;
    margin-bottom: 10px;

    .k-field {
        position: relative;

        .k-input {
            font-size: 18px;
            color: #333333;
            height: 48px;
            background-color: transparent;
            border: 0px;
            border-bottom: 1px solid rgba(153, 153, 153, 0.5);
            border-radius: 0px;
            width: 100%;
            padding: 15px 10px 0px 10px;
            outline: none;
            text-align: left;
            
            @include hidden-placeholder;
            @mixin label-active {
                ~ .k-input-label {
                    color: #999999;
                    top: 12px;
                    font-size: 14px;
                }
                // Icon
                + .icon-input {
                    margin-top: 2px;
                }
            }

            &:focus,
            &:active {
                @include label-active;
            }

            &:not(:placeholder-shown) {
                @include label-active;
            }

            &:-webkit-autofill {
                @include label-active;
            }

            &:not(:-ms-input-placeholder) {
                @include label-active;
            }

            &:read-only, &:disabled {
                background-color: rgba(140,140,140,.1);;
            }
        }

        .k-input-label {
            font-size: 18px;
            color: #333333;
            position: absolute;
            font-weight: normal;
            left: 0px;
            top: 50%;
            transform: translateY(-50%);
            pointer-events: none;
            transition: 0.2s ease all;
            right: 0px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            margin-right: 20px;
        }
    }

    // K-field with icon
    .k-field-icon {
        .k-input {
            padding: 10px 20px 0px 38px;
        }
        .k-input-label {
            left: 38px;
        }

        // Icon
        .icon-input {
            position: absolute;
            width: 14px;
            left: 15px;
            top: 50%;
            transform: translateY(-50%);
            transition: 0.2s ease all;
        }
    }

    // Error
    .k-error {
        color: #ff0100;
        align-items: center;
        margin: 8px 0px;
        transition: opacity 0.3s;
        pointer-events: none;
        visibility: hidden;
        opacity: 0;
        position: absolute;
        z-index: -10;
        display: none;

        .icon-error {
            width: 14px;
            margin-right: 7px;
            margin-top: 1.8px;
        }
    }
}

// EMAIL 
.k-input-field-floating-email {
    .btn-verify {
        height: 40px;
        margin-top: 20px;
    }

    .email-validated-ok {
        width: 21px;
        height: 21px;
        position: ABSOLUTE;
        right: 0;
        top: 15px;
        display: none;
    }

    @media (min-width: 1024px) {
        .btn-verify {
            width: 200px;
            position: absolute;
            right: 0;
            top: -18px;
        }
    }
}
.k-input-field-floating-email-validated {
    .email-validated-ok { display: block; }
    .btn-verify { display: none; }
}

// PASSWORD 
.k-input-field-floating-password {
    .k-field  {
        .k-input {
            padding-right: 45px;
        }
    }

    .btn-eye {
        background-color: transparent;
        margin: 0px;
        padding: 0px;
        border: 0px;
        box-shadow: 0px 0px 0px;
        position: absolute;
        top: 15px;
        right: 0px;
        z-index: 2;
        &:focus {
            outline: none;
        }

        img {
            width: 22px;
            height: 22px;
        }

        .hidden-password { display: none; }
    }

    .btn-eye-show-password {
        .hidden-password { display: block; }
        .show-password { display: none; }
    }

    .error-list {
        color: $c-alert;
        list-style: none;
        margin: 0px;

        .approved {
            display: none;
            color: $c-approves;
            .okey-icon { display: block; }
            .error-icon { display: none; }
        }

        li {
            display: flex;
            align-items: center;
            margin-bottom: 7px;
            font-size: 14px;

            img {
                width: 16px;
                height: 16px;
                margin-right: 10px;
            }

            .okey-icon { display: none; }
            &:last-child { margin-bottom: 0px; }
        }
    }
}

// LOCATION 
.k-input-field-floating-location {
    .k-field  {
        .k-input {
            padding-right: 45px;
        }
    }

    .k-error {
        max-width: 90%;
    }

    .btn-add-location, 
    .btn-remove-location {
        background-color: transparent;
        border: 0px;
        margin: 0px;
        padding: 0px;
        position: absolute;
        right: 0px;
        top: 15px;

        &:focus {
            outline: none;
            background-color: none;
        }

        &:disabled {
            background-color: transparent;
            background: transparent;
            opacity: 0.5;
        }

        img {
            width: 25px;
            height: 25px;
        }
    }
    .btn-add-location { z-index: 3; }

    .character-counter {
        margin: 0px;
        padding: 0px;
        position: absolute;
        right: 5px;
        top: 50px;
        width: 70px;
        text-align: right;
        color: #cdcdcd;
    }
}

// GEOCODER WITH MAPBOX 
.k-input-field-floating-location-geocoder-active {
    .k-field {
        .k-input {
            @mixin label-active {
                ~ .k-input-label {
                    color: #999999;
                    top: 12px;
                    font-size: 14px;
                }
                // Icon
                + .icon-input {
                    margin-top: 2px;
                }
            }

            @include label-active;
        }
    }
}
.k-input-field-floating-location-geocoder {
    .k-field  {
        .k-input {
            pointer-events: none;
            opacity: 0;
            z-index: -10;
            position: absolute;
        }
        .k-input-label { 
            top: 25px;
        }
    }
    .geocoder {
        position: relative;
        z-index: 2;
        svg { display: none; }
        .mapboxgl-ctrl-geocoder--button { display: none; }
        .mapboxgl-ctrl-geocoder {
            width: 100%;
            max-width: 100%;
            box-shadow: 0px 0px 0px;
            background: transparent;

            .mapboxgl-ctrl-geocoder--input {
                font-family: $Main-font;
                width: 100%;
                max-width: 100%;
                font-family: 'Bariol-Regular';
                padding-right: 45px;
                font-size: 18px;
                color: #333333;
                height: 48px;
                background-color: transparent;
                border: 0px;
                border-bottom: 1px solid rgba(153, 153, 153, 0.5);
                border-radius: 0px;
                width: 100%;
                padding: 15px 10px 0px 10px;
                outline: none;
                padding-right: 10%;
                @include hidden-placeholder;
            }  
        }
        .suggestions { 
            position: absolute;
            z-index: 4;
            font-size: 16px !important;
            box-shadow: 0 4px 4px rgba(0,0,0,.124321);
            -moz-box-shadow: 0 4px 4px rgba(0,0,0,.124321);
            -webkit-box-shadow: 0 4px 4px rgba(0,0,0,.124321);
            border: .5px solid #e1e1e1;
            text-align: left;

            .active{
                a { 
                    background: #486973!important;
                    color: #fff!important;
                }
            }
        }
    }
}

// Error active
.k-field-error-floating {
    .k-error {
        position: relative;
        visibility: visible;
        opacity: 1;
        z-index: 1;
        display: flex;

        i { margin-right: 5px; }
    }
}
