.k-show-input-calendar,
.k-show-input-calendar {
    .k-calendar-wrapper {
        display: flex !important;
    }
}

.k-input-field-calendar {
    .k-input-calendar {
        cursor: pointer;
    }

    .k-field {
        .k-input {
            cursor: pointer;
            &:read-only,
            &:disabled {
                background-color: #ffffff;
            }
        }
    }

    .calendar-icon {
        font-size: 22px;
        position: absolute;
        right: 25px;
        top: 0;
        bottom: 0;
        transform: translate(0px, 50%);
        line-height: 0px;
        cursor: initial;
    }

    .k-calendar-wrapper {
        display: flex;
        flex-direction: column;
        // overflow-y: auto;
        max-width: 430px;
        padding: 4px 2px;
        position: absolute;
        z-index: 2;
        width: 100%;
        // overflow: visible;
        overflow: auto;
        display: none;

        .years-list {
            &::-webkit-scrollbar {
                width: 8px;
                height: 8px;
            }
            &::-webkit-scrollbar-track {
                background: #f7f7f7;
            }

            &::-webkit-scrollbar-corner {
                background: #f7f7f7;
            }

            &::-webkit-scrollbar-thumb {
                background: #999999;
                border-radius: 1px;
                border: 2px solid #f7f7f7;
            }

            &::-webkit-scrollbar-thumb:hover {
                background: #c4c4c4;
            }
        }

        .month-year {
            background-color: $c-dropdown-active;
            width: 100%;
            min-width: 278px;
            border-radius: 4px;
            box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
            display: flex;
            align-items: center;
            justify-content: space-around;
            padding: 7px;

            %optionsList {
                width: 100%;
                padding: 0px;
                margin: 0px 0px 20px 0px;
                list-style: none;
                position: absolute;
                top: 0;
                background-color: #ffffff;
                border-radius: 4px;
                box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
                //
                height: 0px;
                width: 0px;
                overflow: hidden;
                pointer-events: none;
                z-index: -2;
                opacity: 0;
                max-height: 700px;
                overflow-y: auto;
                min-width: 60px;

                li {
                    position: relative;
                    color: $c-text;
                    padding: 0px;

                    button {
                        font-size: 18px;
                        width: 100%;
                        border: 0px;
                        margin: 0px;
                        padding: 10px 7px;
                        background: transparent;
                        cursor: pointer;
                        font-weight: normal;
                        text-transform: capitalize;
                        letter-spacing: 0px;
                        color: $c-text;
                        transition: background-color 0.3s;

                        &:hover {
                            background-color: $c-advertisement-3;
                            color: #ffffff;
                        }
                    }
                    .button-list-selected {
                        background-color: $c-dropdown-active;
                        color: #ffffff;
                    }
                }
            }

            %optionListShow {
                height: auto;
                width: 100%;
                opacity: 1;
                pointer-events: all;
                z-index: 1;
                transition: width 0.3s, opacity 0.3s;
            }

            .controls {
                display: flex;
                align-items: center;
                justify-content: center;
                color: #ffffff;
                position: relative;

                .calendar-arrow {
                    width: 12px;
                    height: 12px;
                    position: relative;
                    cursor: pointer;
                    margin-top: -10px;

                    svg {
                        width: 100%;
                        height: 100%;
                        margin: 0px;
                    }
                }
                .calendar-arrow-back {
                    svg {
                        transform: rotate(180deg);
                    }
                }

                .months-list {
                    @extend %optionsList;
                }
                .months-list-show {
                    @extend %optionListShow;
                }
                .month-label {
                    margin: 0px 10px 0px 10px;
                    font-weight: bold;
                    letter-spacing: 1px;
                    cursor: pointer;
                }
            }

            .year-wrapper {
                color: #ffffff;
                font-weight: bold;
                text-align: center;
                letter-spacing: 1px;
                position: relative;

                .year-label {
                    font-weight: bold;
                    cursor: pointer;
                }

                .years-list {
                    @extend %optionsList;
                }
                .year-list-show {
                    @extend %optionListShow;
                }
            }
        }

        table {
            width: 100%;
            color: $c-text;
            background-color: #ffffff;
            border-radius: 0px 4px 4px 0px;
            border: 0px;
            box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
            margin: 0px;

            thead {
                background: none;
                tr {
                    border: 0px;
                }
                th {
                    padding: 10px;
                    font-weight: bold;
                    text-align: center;
                    font-size: 18px;
                    border: 0px;
                    padding: 15px 10px 10px 10px;
                }
            }

            tbody {
                .calendar-row {
                    background: none;
                    border: 0px;

                    td {
                        text-align: center;
                        padding: 5px;
                        position: relative;
                        .kukun-input-calendar-btn {
                            font-size: 18px;
                            margin: 0px;
                            padding: 0px;
                            background-color: transparent;
                            border: 0px;
                            width: 27px;
                            height: 27px;
                            border-radius: 50%;
                            transition: background-color 0.3s, box-shadow 0.3s, color 0.3s;
                            cursor: pointer;
                            font-weight: normal;
                            color: $c-text;
                            letter-spacing: 0px;

                            &:hover {
                                background-color: $c-advertisement-3;
                                box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
                                color: #ffffff;
                            }
                        }
                        .btn-calendar-selected {
                            background-color: $c-dropdown-active;
                            box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
                            color: #ffffff;

                            &:hover {
                                background-color: $c-dropdown-active;
                            }
                        }
                        .calendar-btn-disabled {
                            pointer-events: none;
                            color: #999999;
                        }
                    }

                    &:last-child {
                        td {
                            padding: 5px 5px 15px 5px;
                        }
                    }
                }
            }
        }

        @media (min-width: 360px) {
            overflow: visible;
        }
    }
    .desktop-kukun-input-calendar {
        flex-direction: row;

        .month-year {
            width: 30%;
            min-width: 125px;
            border-radius: 4px 0px 0px 4px;
            display: block;
            padding: 16px 12px;

            .controls {
                margin-bottom: 20px;
            }
        }
        table {
            width: 70%;
        }
    }
}
