.k-grid-wrapper {
    @include displayGrid();
    @include grid-template-columns(100%);
    grid-row-gap: 30px;
    margin: 30px auto !important;

    @media (min-width: 760px) {
        @include grid-template-columns(48% 48%);
        grid-column-gap: 4%;    
        grid-row-gap: 35px;
    }

    @media (min-width: 1200px) {
        @include grid-template-columns(32% 32% 32%);
        grid-column-gap: 2%;
        grid-row-gap: 35px;
    }

    @include only-ie {
        @for $i from 1 through 16 {
            .card, div {
                &:nth-child(#{$i}) {
                    -ms-grid-column: 1;
                    -ms-grid-row: $i + 1;
                }
            }
        }

        .card, div {
            &:nth-child(2) { // 2 -> 1
                -ms-grid-row: 1 !important;
            }
        }

        @media (min-width: 760px) {
            @include grid-template-columns(48% 4% 48%);

            @for $i from 1 through 16 {
                $gridColumn: 1;
                $gridRow: 1;

                @if (($i%2) == 0) {
                    $gridColumn: 3;
                    $gridRow: $i - 1;
                } @else {
                    $gridColumn: 1;
                    $gridRow: $i;                        
                }
                .card, div {
                    &:nth-child(#{$i}) {
                        -ms-grid-column: $gridColumn;
                        -ms-grid-row: $gridRow;
                    }
                }
            }

            .card, div {
                &:nth-child(2) { // 2 -> 1
                    -ms-grid-column: 1 !important;
                    -ms-grid-row: 1 !important;
                }
            }
        }

        @media (min-width: 1200px) {
            @include grid-template-columns(32% 2% 32% 2% 32%);

            $counter: 0;
            $gridRow: 1;
            @for $i from 1 through 16 {
                $counter: $counter + 1;
                $gridColumn: 1;
                
                @if ($counter == 1) {
                    $gridColumn: 1;
                } @else if ($counter == 2) {
                    $gridColumn: 3;
                } @else if ($counter == 3) {
                    $gridColumn: 5;
                }

                .card, div {
                    &:nth-child(#{$i}) {
                        -ms-grid-column: $gridColumn;
                        -ms-grid-row: $gridRow;
                    }
                }

                @if ($counter == 3) {
                    $gridRow: $gridRow + 1;
                    $counter: 0;
                }
            }

            .card, div {
                &:nth-child(2) { // 2 -> 1
                    -ms-grid-column: 1 !important;
                    -ms-grid-row: 1 !important;
                }

                &:nth-child(3) { // 3 -> 2
                    -ms-grid-column: 3 !important;
                    -ms-grid-row: 1 !important;
                }
            }
        }

        @media (min-width: 1600px) {
            @include grid-template-columns(24% 1.3% 24% 1.3% 24% 1.3% 24%);

            $counter: 0;
            $gridRow: 1;
            @for $i from 1 through 16 {
                $counter: $counter + 1;
                $gridColumn: 1;
                
                @if ($counter == 1) {
                    $gridColumn: 1;
                } @else if ($counter == 2) {
                    $gridColumn: 3;
                } @else if ($counter == 3) {
                    $gridColumn: 5;
                } @else if ($counter == 4) {
                    $gridColumn: 7;
                }

                .card, div {
                    &:nth-child(#{$i}) {
                        -ms-grid-column: $gridColumn;
                        -ms-grid-row: $gridRow;
                    }
                }

                @if ($counter == 4) {
                    $gridRow: $gridRow + 1;
                    $counter: 0;
                }
            }

            .card, div {
                &:nth-child(2) { // 2 -> 1
                    -ms-grid-column: 1 !important;
                    -ms-grid-row: 1 !important;
                }
                &:nth-child(3) { // 3 -> 2
                    -ms-grid-column: 3 !important;
                    -ms-grid-row: 1 !important;
                }
                &:nth-child(4) { // 4 -> 5
                    -ms-grid-column: 5 !important;
                    -ms-grid-row: 1 !important;
                }
            }
        }
    }
}
.k-grid-4-col, .k-grid-5-col {
    @media (min-width: 1400px) {
        @include grid-template-columns(24% 24% 24% 24%);
        grid-column-gap: 1.3%;
    }
}
.k-grid-5-col {
    @media (min-width: 1600px) {
        @include grid-template-columns(19% 19% 19% 19% 19%);
        grid-column-gap: 1.19%;
    }
}