@import "cards/article-cards";

.k-card {
    width: 100%;
    background-color: #ffffff;
    border-radius: 10px;
    -o-box-shadow: $k-blur-2;
    box-shadow: $k-blur-2;
    position: relative;

    .k-card-image-bg {
        position: relative;
        background-position: center center;
        background: {
            // image: url(https://materializecss.com/images/office.jpg);
            position: center center;
            repeat: no-repeat;
            size: cover;
        };
        height: 220px;
        border-radius: 10px 10px 0px 0px;
        
        &:before {
            content: "";
            transition: background-color 0.5s;
        }

        a { text-decoration: none; }

        .caption-text {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            padding: 15px;
            overflow: hidden;
            opacity: 0;
            pointer-events: none;
            transition: opacity 0.5s, margin-bottom 0.2s;
            color: #FFFFFF;
            cursor: pointer;
            margin-bottom: -20px;
        }

        &:hover {
            &:before {
                content: "";
                position: absolute;
                border-radius: 10px 10px 0px 0px;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: rgba(0,0,0, 0.7);
            }

            .caption-text {
                opacity: 1;
                pointer-events: all;
                margin-bottom: 0px;
            }
        }
    }
    
    .k-card-body {
        padding: 13px 10px;
        .info-label {
            margin-bottom: 10px;
        }

        .info-label-bottom {
            margin-top: 10px;
        }

        .k-content { line-height: 120%; }

        a {
            text-decoration: none;
        }

        .buttons-wrapper {
            margin-top: 12px;
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            justify-content: space-between;

            button {
                margin-bottom: 10px;
            }
        }
    }
}


// Horizontal 
.k-card-horizontal {
    display: flex;
    align-items: center;

    .k-card-image-bg { 
        width: 40%;
        border-radius: 10px 0px 0px 10px;

        &:hover {
            &:before {
                content: "";
                border-radius: 10px 0px 0px 10px;
            }
        }
    }

    .k-card-body { 
        width: 60%;
        padding: 13px 15px;    
    }
}