.k-input-checkbox {
    display: block;
    width: 22px;
    height: 22px;
    position: relative;
    bottom: -3px;
    margin-bottom: 4px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    display: flex;
    align-items: center;
    margin-bottom: 0px;

    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
        &:checked~.k-input-checkmark {
            border: 1px solid #122448;
            box-shadow: 0px 2px 2px rgba(0, 0, 0, .118829);
            background-color: #122448;

            &:after {
                display: block;
                left: 6px;
                top: 1px;
                width: 6px;
                height: 12px;
                border: solid #fff;
                box-sizing: content-box;
                border-width: 0 2px 2px 0;
                -webkit-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
                transform: rotate(45deg);
    
            }
        }
        &:hover~.k-input-checkmark {
            border: 1px solid #122448;
            box-shadow: 0px 2px 2px rgba(0, 0, 0, .118829)
        }
    }
    .k-input-checkmark {
        position: absolute;
        height: 22px;
        width: 22px;
        background-color: #FFFFFF;
        border: 1px solid #cdcdcd;
        border-radius: 5px;
        transition-duration: .3s;


        &:after {
            content: "";
            position: absolute;
            display: none
        }
    }

    label {
        .check-label {
            margin-bottom: 0px;
            margin-left: 24px;
        }
    }
}